<template>
 <!-- 
                {{getInfoOfMv(mv.id)}} 
                formv.idmvgetInfoOfMv(mv.id)
                

                methods
                    

                    getInfoOfMv(mv.id)datalikedCountgetInfoOfMv(mv.id)
                    likedCountlikedCount
            -->
 <div id="about">
    <img src="../../assets/yuer-mv/mv-thumbs.png" alt="" class="thumb cardIcon" >
    <span>{{mvCount(likedCount)}}</span>
    <img src="../../assets/yuer-mv/mv-comment.png" alt="" class="commends cardIcon">
    <span>{{mvCount(commentCount)}}</span> 
</div>
</template>

<script>

import axios from 'axios'
axios.defaults.baseURL = 'https://autumnfish.cn/'
const getInfoOfMVs = "/mv/detail/info?mvid="

   export default {
       props: {
           mvid:Number
       },
       data(){
            return{
                likedCount:0,
                commentCount:0
            }
        },
        computed: {
          
        },
        mounted () {
            this.getInfoOfMVs()
        },
        methods: {
            mvCount(Count){
                return Count > 10000 ?  String(Count).slice(0,-4)+'' : Count
            },
            /*
                ajax
                
                card

                cardv-for

                ajaxactionstate
                ajax
                
                ajaxdata
            */
            getInfoOfMVs(){
                axios.get(getInfoOfMVs + this.mvid ).then((response) =>{
                    this.likedCount = response.data.likedCount
                    this.commentCount =  response.data.commentCount
                },(error) => {

                })
            }
        }
   }
</script>

<style>
#about{
        margin-left: 5%;
        height: 17%;
        width: 30%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        font-size: 12px;
}
#about > .cardIcon{
    height: 20px;
}
</style>


