<template>
<div>
    <div v-for="(tags,index) in mvtags" :key="index">
        <p class="tags-name">{{tags.name}}</p>
        <div>
            <p class="tags" v-for="(tag,i) in tags.tag" :key="i"  :class=" ((tag === mvtypes.area && tags.name === '')  ||(tag === mvtypes.type && tags.name === '') || tag === mvtypes.order) ? 'bechosed' : ''  " @click="changeType(tags,tag)">{{tag}}</p>
        </div>
    </div>
</div>
</template>

<script>
   export default {
       data(){
            return{
               mvtags: [
                    {
                        name:'',
                        tag:[
                            '',
                            '',
                            '',
                            '',
                            '',
                            ''
                        ],
                    },
                    {
                        name:'',
                        tag: [
                            '',
                            '',
                            '',
                            '',
                            '',
                        ],
                    },
                    {
                        name:'',
                        tag:[
                            '',
                            '',
                            ''
                        ]
                    }
                 ],
            }
        },
        computed: {
            mvtypes(){
                return this.$store.state.mvtypes
            }  
        },
        methods: {
            changeType(tags,tag){
                console.log(tags.name);
                console.log(tag);
                if( tags.name === ''){
                     this.$store.state.mvtypes.area = tag
                }else if( tags.name === ''){
                     this.$store.state.mvtypes.type = tag
                }else if( tags.name === ''){
                     this.$store.state.mvtypes.order = tag
                }
            },
          
        }
   }
</script>

<style>

.tags-name{
    font: 15px/30px"";
    margin: 15px;
}
.tags{
    display: inline-block;
    padding: 5px 15px;
    margin:10px;
    font: 14px "";
    background-color: rgb(238, 238, 238);
    border-radius: 15px;
    text-align: center;
}
.tags > a{
    color: black;
}
.tags > a:active{
    color: black;
}
.bechosed{
    background-color: rgb(248, 13, 13);
    color: white;
}
</style>


